@import '../../styles/mixins';

$large-size: 1320px;
$medium-size: 1024px;
$small-size: 768px;
$smallest-size: 480px;

$font-size-base: 14;
$line-heaight-base: 17px;
$font-family-base: 'Roboto', sans-serif;
$font-family-solid: 'Montserrat', sans-serif;
$font-family-header: 'Fugaz One', cursive;
$font-family-text: 'Open Sans', sans-serif;

$primary-1: #ae0032;
$primary-2: #455162;
$primary-3: #1f2f46;

$neutral-1: #0d1720;
$neutral-2: #636363;
$neutral-3: #dadada;
$neutral-4: #f2f2f2;
$neutral-5: #fcfbfa;

$gray-1: #F1F1F1;
$gray-2: #404040;
$gray-3: #292929;
$gray-4: #333C30;
$gray-5: #F8F8F8;
$gray-6: #D4D4D4;

$orange: #EB5B1F;
$orange-1:#B44619;
$orange-hover:#DC6331;

$green-1: #1B3A1B;

.popup {
  display: none;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 99999;
}

.popup-visible {
  display: block;
}

.close-popup {
  color: $gray-6;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  cursor: pointer;
  position: absolute;
  top: 30px;
  right: 30px;
  transition: all 0.6s;
  box-sizing: content-box;
  &:hover {
    color: $orange;
  }
  @include phone {
    top: 20px;
    right: 15px;
    line-height: 28px;
  }
}

.popup-body {
  border-top: 5px solid $orange;
  background: url('images/bg-popup.png'); 
  background-repeat: no-repeat;
  background-position: bottom left;
  background-color: white;
  padding: 92px 72px 40px;
  margin: 100px auto;
  max-width: 874px;
  position: relative;
  @include phone {
    padding: 20px 15px;
  }
}

.popup-header {
  font-family: $font-family-header;
  font-size: 40px;
  line-height: 52px;
  text-transform: uppercase;
  margin-bottom: 10px;
  @include phone {
    font-size: 28px;
    line-height: 36px;
  }
}